.container {
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.stopwatch {
  --hour-duration: 3600s;
  --minute-duration: 60s;
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch--speed-up {
  --hour-duration: 600s;
  --minute-duration: 10s;
}

.stopwatch__axis-pin {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: calc(50% - 2.5vmin);
  left: calc(50% - 2.5vmin);
}

@keyframes stopwatch-arm {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

.stopwatch__minutes-arm {
  animation: stopwatch-arm var(--hour-duration) steps(60, end) infinite;
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  position: absolute;
  bottom: 50%;
  left: calc(50% - 1.5vmin);
}

.stopwatch__seconds-arm {
  animation: stopwatch-arm var(--minute-duration) linear infinite;
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  position: absolute;
  bottom: 50%;
  left: calc(50% - .75vmin);
}
